* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Times New Roman", Times, serif;
}

.App {
  background-image: url("https://i.pinimg.com/originals/a6/f7/cf/a6f7cfb75a629919c7bb4bcdc0048d5f.jpg");
  height: 100vh;
  background-position: bottom;
  background-size: cover;
  transition: 0.4 ease-out;
  padding: 20px;
}

.app_cold {
  height: 100vh;
  background-image: url("https://img.wallpapersafari.com/tablet/768/1024/93/60/kcz1xg.jpg");
}

.search-box {
  display: flex;
  justify-content: center;
}

.search-bar {
  font-size: medium;
  margin-top: 50px;
  width: 80%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  appearance: none;
  background: none;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
}
.location-box {
  margin-top: 200px;

  line-height: 40px;
  font-size: 29px;
  font-weight: 600;
  text-align: center;
}

.weather-box {
  line-height: 40px;
  margin-top: 20px;
  text-align: center;
  font-size: x-large;
  font-weight: 600;
}

.weather-info {
  font-weight: 500;
  font-size: large;
  text-transform: capitalize;
}
